* {
  margin: 0;
  padding: 0;
}

html {
  background-color: #000000;
}

.App {
  display: flex;
  padding: 1cm;
  justify-content: flex-end;
  align-items: flex-start;
  max-height: calc(100vh -2cm);
  overflow: auto;
}

.time-text {
  color: rgb(238, 238, 238);
  font-size: 80px;
  font-family: "Jersey 25";
}

.period {
  font-size: 40px;
}

.time-box {
  padding: .7cm 1cm;
  background-color: #0f0f0f;
  /* margin-top: 2cm; */
  backdrop-filter: blur(10px);
  border-radius: 10px;
}